    export default{
//        components: {ElMenuItem},
        data(){
            return {
                defaultActive : 'query',
                navBarItems: [
                    {
                        icon: 'el-icon-search',
                        index: '/home/query',
                        title: '查询会议',
                    }, {
                        icon: 'el-icon-menu',
                        index: '/home/create',
                        title: '添加会议',
                    }, {
                        icon: 'el-icon-setting',
                        index: '/home/settings',
                        title: '系统设置',
                    },
                ]
            }
        },
        methods: {
            confirmLeave() {
                let _this = this;
                this.$confirm('是否退出系统?', '提示', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning'
                }).then(() => {

                    axios.post('/logout', {}).then((response) => {
                        console.log(response.data);
                        if(response.data.isLogout){
                            _this.$router.push('/login');
                        }
                    }).catch((error) => {
                        console.log(error);
                    });

                }).catch(() => {
//                    this.$router.go(0);
                    this.$message({
                        type: 'info',
                        message: '已取消'
                    });

                })
            }
        },

        // computed:{
        //           onRoutes(){
        //           	console.log(this.$route.path.replace('/',''))
        //               return this.$route.path.replace('/','');
        //           }
        //       }
    }



